import React, { Component } from "react";
import { useState, useEffect } from 'react';
import {BrowserRouter as Router,Switch,Route,Link,Redirect,} from "react-router-dom";
import MyStyledInput from "./MyComponents"
import MyStyledfield from "./MyComponents"
import $ from 'jquery';
import styles from './css/blog.module.css';
import stories from './stories.json';
import PostDetApp from "./post_detail"

import csrftoken from  "./csrf"
import handleSubmit, {handleUpdate} from './postForm'

import parse from 'html-react-parser';
import { CKEditor } from 'ckeditor4-react';
// import { CKEditor } from '@ckeditor/ckeditor4-react';
// import ClassicEditor from '@ckeditor/ckeditor4-build-classic';

const color =['orange','red','blue','green','lilac','yellow','violet']

export default function Example(){
  const [count, setCount] = useState(0);
  const [blogs, setBlogs] = useState('');
  const [textValue, setTextValue] = useState('');
  // const [editorState, setEditorState] = React.useState(
  //   () => EditorState.createEmpty(),
  // );
  // const [value, setValue] = useState('');
  console.log(textValue)
  useEffect(() => {
    $('#blog-form').hide(0);
    // console.log(textValue)
    if ($('#mobile-text-filed')){
      console.log('owanbe .')
    }
    fetchTasks();
  },[]);

  let showcontent =(c)=>{
    $('#blog-form').toggle(600);
    $('#add-button').toggle(600);
  }
  
  function shareStory(event){
    event.preventDefault();
    const url='http://localhost:8000/api/blog/';
    const formData = new FormData(event.target);
    const data = {
      'subject': formData.get('Subject of your story'),
      'topic': formData.get('related topics'),
      'content': textValue,
      'author_email': formData.get('Email'),
    }
    console.log(data)
    // handleSubmit(data,url,'POST');
    let m = handleSubmit(data,url,'POST');
    console.log(m)

  }
  const fetchTasks = (() => {
    console.log('Fetching...')

    fetch('http://localhost:8000/api/blog/')
    .then(response => response.json())
    .then(data =>setBlogs(data))
  })  




  function updatestory(event,p_id){
    const url='http://localhost:8000/api/blog/'+p_id;
    const data = {
      'update_count':1
    }
    handleUpdate (data,url)

  }
  return (
      <Switch>
       <div className={styles["contentb" ]}>
          
          {blogs&&blogs.map((story,index) =>
            <div onClick={(e) => updatestory(e,story.id)} className={styles["box"]} style={{borderTopColor: color[index],height:'23em'}}> 
            <span className={styles["story-topic"]}>{story.topic}</span>
            <h5 className={styles["story-subject"]}>{story.subject.toUpperCase()}</h5>
            <p className={styles["story-content"]}>{parse(story.content)}</p>
            <img alt='' className={styles["story-avatar"]} src={require("./media/ml.jpg").default}  />
            <p> <i className={styles["views-eye"] +' '+"fa fa-eye"} aria-hidden="true"></i>
                <span>{story.views}</span>
            </p>
            </div>
            )
        }
        <button onClick={(e) => showcontent('#blog-form')} id="add-button" className={styles["add-button"]} >
            <i class="fa fa-plus" aria-hidden="true"></i></button>
        <div className={styles["blog-form"]} id="blog-form" > 
        <button onClick={(e) => showcontent('#add-button')} className={styles["cancel-button"]}>
        <i class="fa fa-close" aria-hidden="true"></i></button>
        <span className={styles["tag-span"]} > Share your sory with the world!!</span>
        <form onSubmit = {shareStory}  id="message-form-box" class={[styles['fc'],styles['blog-form-box']].join(' ')} >
            
            <div className={styles["fr"]}>
              <div>
                <MyStyledfield className={styles["bny"]} type='text' bgc='rgba(140,140,150,6)' height='3em' width='100%' name='Email' id='name_input' />
              </div>
              <div>
                <MyStyledfield className={styles["bny"]} type='text' bgc='rgba(140,140,150,6)' height='3em' width='100%' name='related topics' 
              id='name_input' />
              </div>
              

              
              

              
            </div>
            <div className={styles["frvv"]}>
              <MyStyledfield className={styles["bny"]} type='text' bgc='rgba(140,140,150,6)' height='3em' width='100%' name='Subject of your story' id='name_input' />
            </div>
            <div class={[styles['bny'],styles['mobile-text-filed']].join(' ')} id='mobile-text-filed'>
              <MyStyledfield className={styles["bny"]} type='field' rows='5' bgc='rgba(140,140,150,6)' height='13em' width='100%' name='let your story goes here' id='mtext_input' />
            </div>
            <div className={styles["frck"]}>
              <CKEditor className={styles["frck"]}
              initData={<p><b>  Your story goes here:</b></p>}
              onBlur={( { editor } )  => {
                const data = editor.getData();
                setTextValue(data);
                console.log({textValue});
                console.log( { editor, data } );
              }}
              />
            </div>
             <div>
               <button className={styles["sub-butt"]}  >
                <span>Tell the World </span>
                <i class="fa fa-globe" aria-hidden="true"></i>
              </button>
             </div>
          </form>
        </div>
        
        </div>
        
          <Route path="/fr/jknj" component={PostDetApp} ></Route>
        </Switch>
  );
}