import React from "react"
import { useState, useEffect } from 'react';
import { compose, withProps } from "recompose"
import validator from 'validator';
import { withScriptjs, withGoogleMap, GoogleMap, Marker } from "react-google-maps"
import $ from 'jquery';
import { CKEditor } from 'ckeditor4-react';

export default function MyStyledtextarea(props) {

  const [validation, setValidation] = useState({'EmailVal':'#1affa3','error':''});
  useEffect(() => {
      //showcontent("#frontend")
    })
    const showcontent = function(e){
      $(e.target).parent().find(".labaltag").css({"top":"0px",'color':'#1affa3',left:'1px'});
      $(e.target).parent().find(".baseline").toggle(600)
      //$(e.target).parent().css({"border-bottom":"2px solid green",'position':'absolute'});
    }
    const outfocus = function(e){
      $(e.target).parent().find(".baseline").toggle(600)
      if (e.target.value==''){
        $(e.target).parent().find(".labaltag").css({"top":"",'color':'',left:'1vw'});
      }      
    }
    let handleChange=(event)=> {
      //this.setState({value: event.target.value});
      if (event.target.name=='Email'){
        if  (validator.isEmail(event.target.value)){
          setValidation({'EmailVal':'#1affa3'})
        }
        else{
          setValidation({'EmailVal':'red'})
        }
      } //=> true
      
    }
  let tf='none'
  const mystyle = {
  form:{color: "white",backgroundColor:props.bgc,fontFamily: "Arial",position:'relative',display:'flex',
          'flex-direction':'column','align-atems':'center','min-height':props.height,'justify-content':'center',width:props.width,'border-radius':'6px',
          margin:'0.5vw'},
      input:{position:'absolute',bottom:'0px',left:"0",backgroundColor:'rgba(255,255,255,0)',outline:'none',border:'0px',width:'100%',height:'80%',
        'fontSize':'1.6vw','paddingLeft':'1vw'},
      labaltag:{left:'1vw','position':'absolute'},
      baseline:{backgroundColor:validation.EmailVal,height:'2px',position:'absolute',bottom:'0px',display:"none",width:'100%','marginTop':'1px'},
    };

    if (props.type=='field') {
      tf =<textarea onFocus={(e) => showcontent(e)} rows={props.row}  onBlur={(e) => outfocus(e)} style={mystyle.input} type={props.type} id={props.id} name={props.name}/>
    }
    else if (props.type=='bigfield') {
      tf= <CKEditor id={props.id} name={props.name} initData={props.name} />
    }
     else {
      tf = <input onFocus={(e) => showcontent(e)} onChange={handleChange} onBlur={(e) => outfocus(e)} style={mystyle.input} type={props.type} id={props.id} name={props.name}/>
    }
  return (

      




    <div className="form-input" style={mystyle.form}>
      <span className="labaltag" style={mystyle.labaltag}>{props.name}</span>
      <area shape="" coords="" href="" alt=""/>
      {tf}
      <div className="baseline" style={mystyle.baseline}></div>
    </div>
  )
}