
import csrftoken from  "./csrf"
import DateObject from "react-date-object";
import $ from 'jquery';
import parse from 'html-react-parser';



  //let date = new DateObject({date:"2020/08/01",format: "YYYY-MM-DDThh:mm[:ss[.uuuuuu]][+HH:MM|-HH:MM|Z]"})

  
  export default function handleSubmit (data,url,meth){
    let blogdatares
    let res
    fetch(url, {
    method: meth, // or 'PUT'
    headers: {
      'Content-Type': 'application/json',
      'X-CSRFToken':csrftoken,
    },
    body: JSON.stringify(data),
    })
    .then(response =>{res=response;return response.json()})
    .then(data =>{if (res.status<300 & meth=='POST'){
      blogdatares=data;
      alert(data)
    }
    })
    .catch(error=> alert(error))
    return blogdatares
    

  }
  export function handleUpdate (data,url){
    console.log(data);
    console.log(url);
    handleSubmit (data,url,'PUT')
   }