import React, { Component } from "react";
import { render } from "react-dom";
import { useState, useEffect } from 'react';
import {BrowserRouter as Router,Switch,Route,Link,Redirect,} from "react-router-dom";
import $ from 'jquery';


import styles from './css/App.module.css';

import HomePage from "./HomePage";
import AboutApp from "./about";
import SkillApp from "./skills";
import ContactApp from "./contact"
import BlogApp from "./blog"
import ProjectApp from "./works"
import PostDetApp from "./post_detail"



  function App() {
  const [count, setCount] = useState(0);
  //let history = useHistory();
 
  // Similar to componentDidMount and componentDidUpdate:
  useEffect(() => {
      //showcontent("#hidecontent")
        $(".klklkl").children().click(function(){
          if ($(window).width()<=768) {
            showcontent("#showcontent");
          }
        });

        if ($(window).width()>768) {
           $('.navb').show(900);
          }
        else {
        // $('.navb').show(900);
        showcontent("#showcontent")
      }
    })
    const showcontent = function(c){
     $('.navb').toggle(900);
     $('.fafufu').hide(900);
     $(c).toggle(900);    
   }

 const sodiqurl = "https://fontmeme.com/permalink/210708/404aa116cb625cf9f292aa88313b03ea.png"
  return (
      <Router basename='/'>
      <div className={[styles['appnin'],''].join(' ')}>
        <div className={[styles.fc,'navb', styles['navb']].join(' ')}>
            <div className={[styles.fc, styles.linktag].join(' ')}>
                <Link className={[styles.fc, styles.link, 'klklkl'].join(' ')} to="/">
               
                  <img src= {sodiqurl} alt="S"  border="0"></img>
                    <p><b>Sodiq </b></p>
                    <br></br>
                </Link>
                <span>software developer</span>
            </div>
          
          <ul className={[styles.fc, 'klklkl'].join(' ')} id='link-dir'>
            <Link to="/fr/about"><a>About</a></Link>
            <Link to="/fr/skills"><a>Skills</a></Link>
            <Link to="/fr/projects"><a>Work</a></Link>
            <Link to="/fr/blog"><a>Blog</a></Link>
            <Link to="/fr/contact"><a>Contact</a></Link>
            
            <div>
              <a href="#"><i className="fa fa-instagram" aria-hidden="true"></i></a>
              <a href="https://www.linkedin.com/in/akinyemi-sodiq-70bb2019a/"><i class="fa fa-linkedin" aria-hidden="true"></i></a>
              <a href="https://www.facebook.com/akinyemi.sodiq3"><i class="fa fa-facebook" aria-hidden="true"></i></a>
              <a href="#"><i class="fa fa-twitter" aria-hidden="true"></i></a>
              <a href="https://github.com/mallamsiddiq"><i class="fa fa-github" aria-hidden="true"></i></a>
            </div>
          </ul>
        </div>        
          
        <div className={styles.content}>
            <span className={[styles['txt-v'], styles.dl].join(' ')}> scroll to bottom &#x2192; </span>
            <Switch>
              <Route exact path="/" component={HomePage}>
              </Route>
              <Route exact path="/fr/about" component={AboutApp} ></Route>
              <Route path="/fr/jk" component={PostDetApp} ></Route>
              <Route path="/fr/skills" component={SkillApp} ></Route>
              <Route path="/fr/contact" component={ContactApp} ></Route>
              <Route path="/fr/projects" component={ProjectApp} ></Route>
              <Route path="/fr/blog" component={BlogApp} ></Route>
            </Switch>
            <span className={[styles['txt-v'], styles.dr].join(' ')}> scroll to bottom &#x2192;</span>
            
        </div>
        <div className={styles["open-close"]}>
          <i className={[styles["open-close-baby"],'fafufu', 'fa', "fa-bars"].join(' ')} id="showcontent" onClick={(e) => showcontent("#hidecontent")}></i>
          <i className={[styles["open-close-baby"],'fafufu', 'fa', "fa-times"].join(' ')} id="hidecontent" aria-hidden="true" 
           onClick={(e) => showcontent('#showcontent')}></i>
      </div>
      
      </div>

      </Router>

  );
}
export default App ;