import React, { Component } from "react";
import { useState, useEffect } from 'react';
import {BrowserRouter as Router,Switch,Route,Link,Redirect,} from "react-router-dom";
import MyStyledInput from "./MyComponents"
import MyStyledfield from "./MyComponents"
import $ from 'jquery';
import styles from './css/blog.module.css';
import stories from './stories.json';

import csrftoken from  "./csrf"
import handleSubmit, {handleUpdate} from './postForm'

const color =['orange','red','blue','green','lilac','yellow','violet','brown']

export default function Example(){
  const [count, setCount] = useState(0);
  const [blogs, setBlogs] = useState('');
  useEffect(() => {
    $('#blog-form').hide(0);
  });

  let showcontent =(c)=>{
    $('#blog-form').toggle(600);
    $('#add-button').toggle(600);
  }


  return (
       <div className={styles["contentb" ]}>
          {stories.map((story,index) =>
            <div  className={styles["box"]} style={{borderTopColor: color[index],minHeight:'20em'}}> 
            <span className={styles["story-topic"]}>{story.topic}</span>
            <h5 className={styles["story-subject"]}>{story.subject.toUpperCase()}</h5>
            <p className={styles["story-content"]}>{story.content}</p> 
            <img alt='ss' className={styles["story-avatar"]} src={require("./media/ml.jpg").default} />
            
            </div>
            )
        }
        </div>
  );
}