import React, { Component } from "react";
import { useState, useEffect } from 'react';
import {BrowserRouter as Router,Switch,Route,Link,Redirect,} from "react-router-dom";
import GoogleApp from "./googleapp"
import MyStyledInput from "./MyComponents"
import MyStyledfield from "./MyComponents"
//import $ from 'jquery';
import styles from './css/contact.module.css';
import csrftoken from  "./csrf"
import handleSubmit, {handleUpdate} from './postForm'


export default function Example() {
  const [count, setCount] = useState(0);

  useEffect(() => {
    })

  //const handleSubmit;
  //const showcontent = function(c){
  const handleChange=function(event){
    this.setState({value: event.target.value});
  }
  function postMessage(event){
    event.preventDefault();
    const url='http://localhost:8000/api/message/';
    const formData = new FormData(event.target);
    const data = {
      'title': formData.get('Subject'),
      'name': formData.get('Name'),
      'content': formData.get('Message'),
      'email': formData.get('Email'),
    }
    handleSubmit(data,url,'POST');

  }


  return (
        <div className={styles["contactteli"]} >
          <div className={[styles['contactteli-form-box'],styles['fc']].join(' ')} >
             <p className={styles["contacsodiq"]}>
              Wanna build something great?
            </p>
            <p>
              Contact Sodiq
            </p>
            <form onSubmit = {postMessage}  id="message-form-box" class={[styles['fc'],styles['contact-form-box']].join(' ')} >
              <div className={styles["fr"]}>
                <MyStyledfield className={styles["bny"]} type='text' bgc='rgba(140,140,150,6)' height='4em' width='50%' name='Name' id='name_input' />
                <MyStyledfield className={styles["bny"]} type='text' bgc='rgba(140,140,150,6)' height='4em' width='50%' name='Email' id='name_input' />
              </div>
              <div className={styles["fr"]}>
                <MyStyledfield className={styles["bny"]} type='text' bgc='rgba(140,140,150,6)' height='4em' width='100%' name='Subject' id='name_input' />
              </div>
              <div className={styles["fr"]}>
                <MyStyledfield className={styles["bny"]} type='field' bgc='rgba(140,140,150,6)' height='14em' width='100%' name='Message' id='name_input' />
              </div>
               <div>
                 <button>
                  <span>SEND</span>
                  <i class="fa fa-paper-plane" aria-hidden="true"></i>
                </button>
               </div>
               <div className={styles['foot-tag']}>
                 <i>let's turn your curiosity into reality</i>
               </div> 
            </form>
          </div>
          <div >
            <GoogleApp/>
          </div>  
        </div>
  );
}